import { NextResponse } from 'next/server';
import type { NextRequest } from 'next/server';

// Access the global scans Map
declare global {
  var scans: Map<string, any>;
}

export async function GET(
  request: NextRequest,
  { params }: { params: { scanId: string } }
) {
  try {
    const scanId = params.scanId;
    console.log(`[Progress API] Fetching progress for scan ${scanId}`);
    
    if (!global.scans) {
      console.log('[Progress API] Scans map is not initialized');
      global.scans = new Map();
    }
    
    console.log('[Progress API] Available scans:', Array.from(global.scans.keys()));
    
    const scan = global.scans.get(scanId);
    console.log('[Progress API] Found scan:', scan);
    
    if (!scan) {
      console.log(`[Progress API] Scan ${scanId} not found`);
      return NextResponse.json(
        { error: 'Scan not found' },
        { status: 404 }
      );
    }

    return NextResponse.json({
      scanId: scan.scanId,
      status: scan.status,
      progress: scan.progress
    });
  } catch (error) {
    return NextResponse.json(
      { error: 'Failed to get scan progress' },
      { status: 500 }
    );
  }
}
